<template>
  <div id="app">
    <div class="left">
      <div class="edit">
        <h3>边框样式编辑器</h3>
        <p>可以输入各个角的圆弧角度来预览形状,记得带单位(￣︶￣)</p>
        <div class="inputBox">
          <input type="range" min="0" max="50" v-model="boxSty.borderTopLeftRadius" />左上 <input type="range" min="0" max="50" v-model="boxSty.borderTopRightRadius" />右上 <input type="range" min="0" max="50" v-model="boxSty.borderBottomLeftRadius" />左下
          <input type="range" min="0" max="50" v-model="boxSty.borderBottomRightRadius" />右下
          <hr />
          <div><input type="range" min="0" max="400" v-model="boxSty.width" />宽度 <input type="range" min="0" max="400" v-model="boxSty.height" />高度</div>
        </div>
        <hr />
        <input type="color" value="#444" v-model="boxSty.backgroundColor" />
      </div>

      <div class="copy">
        <h3>以下是各个角的样式,可供复制! OvO</h3>
        <p>border-top-left-radius: {{ this.oneSize.borderTopLeftRadius }};</p>
        <p>border-top-right-radius: {{ this.oneSize.borderTopRightRadius }};</p>
        <p>border-bottom-right-radius: {{ this.oneSize.borderBottomLeftRadius }};</p>
        <p>border-bottom-left-radius: {{ this.oneSize.borderBottomRightRadius }};</p>
      </div>
    </div>

    <div class="right">
      <div class="bigBox">
        <div class="box" :style="oneSize"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  data() {
    return {
      boxSty: {
        borderTopLeftRadius: 0,
        borderTopRightRadius: 0,
        borderBottomLeftRadius: 0,
        borderBottomRightRadius: 0,
        width: '400',
        height: '400',
        backgroundColor: '#444'
      }
    }
  },
  computed: {
    oneSize() {
      return {
        borderTopLeftRadius: this.boxSty.borderTopLeftRadius + '%',
        borderTopRightRadius: this.boxSty.borderTopRightRadius + '%',
        borderBottomLeftRadius: this.boxSty.borderBottomLeftRadius + '%',
        borderBottomRightRadius: this.boxSty.borderBottomRightRadius + '%',
        width: this.boxSty.width + 'px',
        height: this.boxSty.height + 'px',
        backgroundColor: this.boxSty.backgroundColor
      }
    }
  }
}
</script>

